      </p>
      <div
        :class="changeSubDescPadding + ' mt-7'"
        style="border-left: 0.5vmin solid #FFE52D; border-right: 0.5vmin solid #FFE52D;"
      >
        <p :class="bodyClasses + ' font-weight-bold text--darken-2'">
          From transforming our justice system to ensuring education equity,
          changemakers are doing some of the most vital work there is.
          Communications is the secret sauce that changemakers can use to
          succeed in their work to make our communities and our world better.
          Change Consulting, LLC, works exclusively with nonprofits. foundations
          and mission-driven businesses to raise awareness, change hearts and
          minds, and inspire action.
        </p>
      </div>
    </div>
    <div style="padding: 3vmin 10vmin 3vmin 10vmin;" class="primary">
      <p :class="changeDescClasses">
        We specialize in helping changemakers identify,
        create and share their stories for powerful impact.
      </p>
    </div>
    <div style="padding: 5vmin 10vmin 5vmin 10vmin;">
      <div
        :class="changeSubDescPadding"
        style="border-left: 0.5vmin solid #FFE52D; border-right: 0.5vmin solid #FFE52D;"
      >
        <p :class="bodyClasses + ' font-weight-bold text--darken-2'">
          We offer integrated communications, content development and marketing,
          digital media and earned media consulting services to help changemakers
          and organizations achieve their programmatic, policy and fundraising goals.
          Above all, we strive to become close collaborators and invaluable partners
          to our clients listening deeply, strategizing creatively. and delivering
          results continuously.
          <br/><br/>
          Change Consulting provides the expertise of Bilen Mesfin Packwood and her
          team in public relations and marketing. with a particular focus in the areas
