          reproductive justice and education equity. We are a woman- and minority-owned
          boutique firm based in Oakland, California. and founded in 2008.
        </p>
      </div>

      <div :class="titlePadding">
        <p
          :class="`underline text-center font-weight-bold ma-0 ${titleSize}`"
        >
          OUR WORK
        </p>
      </div>

      <div class="flow-text member-card-container">
        <member-card
          v-for="(card, i) in memberCards" :key="i"
          :avatar="card.src"
          :name="card.name" :shortName="card.shortName"
          :onMemberClick="() => { showInfo(card); }"
        ></member-card>
      </div>
    </div>

    <member-info
      :avatar="focusedMember.src"
      :title="focusedMember.title"
      :description="focusedMember.description"
      ref="memberInfo"
    ></member-info>
  </div>
</template>

<script>
import MemberCard from '../components/MemberCard.vue';
import MemberInfo from '../components/MemberInfo.vue';
import BilenPhoto from '../assets/images/avatars/bilen.jpg';
import LaylaPhoto from '../assets/images/avatars/layla.jpg';

export default {
  data: () => ({
    memberCards: [
      {
        src: BilenPhoto,
        name: 'Bilen Mesfin Packwood',
        shortName: 'Bilen',
        title: 'Founder and Principal - Bilen Mesfin Packwood',
        description: 'With 15 years of experience in media and communications, Bilen Mesfin Packwood is the founder and principal of Change Consulting, LLC. She has established a reputation as a trusted strategic communicator and advisor to some of Northern California’s leading changemakers.<br/><br/>Prior to founding Change Consulting, Bilen advanced communications efforts for then San Francisco District Attorney Kamala D. Harris. While there, she created and implemented strategies to roll out the District Attorney’s key initiatives, such as truancy and reentry; led outreach to local, statewide and national multicultural media; helped manage crisis communications issues as they arose; and served as the Office’s day to-day print and on-camera spokesperson.<br/><br/>As a journalist, Bilen has written for the Associated Press, the Nashville Tennessean, Edutopia and Art & Antiques, among other publications. She has a Master’s in Journalism from the University of California, Berkeley, and a BA in English from Vanderbilt University in Nashville, Tennessee. Connect with Bilen on twitter: @bmesfin.',
      },
      {
        src: LaylaPhoto,
        name: 'Layla Crater',
        shortName: 'Layla',
        title: 'Senior Communications Manager - Layla Crater',
        description: 'Layla Crater joined Change Consulting, LLC, as a communications coordinator in August 2016, helping drive PR and media relations for changemakers. Previously, she served as Account Executive and Showroom Manager for a boutique PR agency in San Francisco, managing media relations and providing operational support for some of the Bay Area\'s most beloved brands, such as Blue Bottle Coffee and Timbuk2. She first discovered her passion for working for social change and nonprofit communications while working in documentary film production as a Production Manager for a PBS Frontline Film, "END GAME: AIDS in Black America." She holds a BA from UC Davis in Communications with an emphasis in American Studies. Layla has lived in Oakland since 2011 and is dedicated to serving her community.',
      },
    ],
    focusedMember: {},
  }),
  computed: {
    titleSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'title';
        case 'sm':
          return 'headline';
        case 'md':
          return 'display-1';
        case 'lg':
          return 'display-2';
        default:
          return 'display-2';
      }
    },

    bannerClasses() {
      const baseClasses = 'primary white--text display-2 text-center banner-text font-weight-black';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} ${this.titleSize} pa-0`;
        case 'sm':
          return `${baseClasses} ${this.titleSize} pa-2`;
        case 'md':
          return `${baseClasses} ${this.titleSize} pa-3 mb-8`;
        case 'lg':
          return `${baseClasses} ${this.titleSize} pa-4 mb-12`;
        default:
          return `${baseClasses} ${this.titleSize} pa-4 mb-12`;
      }
    },

    changeDescClasses() {
      const baseClasses = 'font-weight-bold grey--text text--darken-4 pa-0 ma-0';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-1`;
        case 'sm':
          return `${baseClasses} title`;
        case 'md':
          return `${baseClasses} headline`;
        case 'lg':
          return `${baseClasses} display-1`;
        default:
          return `${baseClasses} display-1`;
      }
    },

    bodyClasses() {
      const baseClasses = 'grey--text';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-2`;
        case 'sm':
          return `${baseClasses} body-1`;
        case 'md':
          return `${baseClasses} title`;
        case 'lg':
          return `${baseClasses} headline`;
        default:
          return `${baseClasses} headline`;
      }
    },

    changeSubDescPadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'px-5 py-1';
        case 'sm':
          return 'px-7 py-2';
        case 'md':
          return 'px-9 py-3';
        case 'lg':
          return 'px-11 py-4';
        default:
          return 'px-11 py-4';
      }
    },

    quoteHeight() {
      return 350;
    },

    titlePadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'py-3';
        case 'sm':
          return 'py-5';
        case 'md':
          return 'py-7';
        case 'lg':
          return 'py-9';
        default:
          return 'py-9';
      }
    },
  },
  methods: {
    showInfo(info) {
      this.focusedMember = info;
      this.$refs.memberInfo.show();
    },
  },
  components: { MemberCard, MemberInfo },
};
</script>

<style scoped>
.underline {
  text-decoration: underline !important;
  text-decoration-color: #FFE52D !important;
}

