<template>
  <v-dialog
    v-model="open"
    :max-width="dialogWidth"
    content-class="info-content"
  >
    <v-sheet
      class="info-sheet" color="white"
      v-if="$vuetify.breakpoint.smAndDown"
    >
      <div class="d-flex flex-row align-center" style="width: 100%; margin-bottom: 3vmin">
        <div class="info-avatar flex-grow-1" :style="avatarStyle">
          <img :src="avatar" style="border-radius: 50%;" width="100%">
        </div>
        <div :class="bodyClasses">
          <p class="font-weight-bold grey--text text--darken-4" style='font-size: 1.25em;'>
            {{ title }}
          </p>
        </div>
      </div>

      <p
        :class="bodyClasses + 'font-weight-medium t grey--text text--darken-3'"
        v-html="description"
      >
      </p>
    </v-sheet>

    <v-sheet
      class="info-sheet" color="white"
      v-if="$vuetify.breakpoint.mdAndUp"
    >
      <div class="info-avatar" :style="avatarStyle + ' margin-bottom: 3vmin; float:left;'">
        <img :src="avatar" style="border-radius: 50%;" width="100%">
      </div>

      <p :class="bodyClasses">
        <span class="font-weight-bold grey--text text--darken-4" style='font-size: 1.25em;'>
          {{ title }}
        </span>
        <br/><br/>
        <span class="font-weight-medium grey--text text--darken-3" v-html="description">
        </span>
      </p>
    </v-sheet>
  </v-dialog>
</template>

<script>
export default {
  props: ['avatar', 'title', 'description'],
  data: () => ({
    open: false,
  }),
  computed: {
    dialogWidth() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 350;
        case 'sm':
          return 500;
        case 'md':
          return 650;
        case 'lg':
          return 800;
        default:
          return 800;
      }
    },

    avatarSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 125;
        case 'sm':
          return 150;
        case 'md':
          return 175;
        case 'lg':
          return 200;
        default:
          return 200;
      }
    },

    avatarStyle() {
      return `min-width: ${this.avatarSize}px; max-width: ${this.avatarSize}px; min-height: ${this.avatarSize}px; max-height: ${this.avatarSize}px;`;
    },

    bodyClasses() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'body-2';
        case 'sm':
          return 'body-1';
        case 'md':
          return 'title';
        case 'lg':
          return 'headline';
        default:
          return 'headline';
      }
    },
  },
  methods: {
    show() {
      this.open = true;
    },
  },
};
</script>

<style>
div .info-content {
  width: 100%;
  border-width: 0.5vmin;
  border-color: #FFE52D !important;
  border-style: solid;
}

div .info-sheet {
  width: 100%;
  padding: 3vmin;
}

.info-avatar {
  border-radius: 50%;
  margin-right: 3vmin;
}
</style>
